/**
 *   min  max 
 *   min  max 
 **/
const numberRange = function () {
  /**    **/
  document.addEventListener('input', function (e) {
    let { target } = e
    let { nodeName, type, min, max, value } = target
    
    /** input,  number **/
    if (nodeName === 'INPUT' && type === 'number') {
      
      /** type number **/
      value = Number(value)
      
      /** min **/
      if (min && value < Number(min)) {
        target.value = min
      }
      
      /** max **/
      if (max && value > Number(max)) {
        target.value = max
      }
    }
  })
}

/**
 *  (e, E, +)
 *   -
 *
 *   -  -
 **/
const onlyNumber = function () {
  document.addEventListener('keydown', function (e) {
    let { target, key } = e
    let { nodeName, type, value } = target
    
    /** input,  number **/
    if (nodeName === 'INPUT' && type === 'number') {
      /**  +, e, E **/
      if ([ '+', 'e', 'E' ].indexOf(key) !== -1) {
        e.preventDefault()
      }
      
      /** -  **/
      if (key === '-' && value.length) {
        e.preventDefault()
      }
    }
  })
}

/**
 *  
 **/
const betterInputNumber = function () {
  numberRange()
  onlyNumber()
}

export { betterInputNumber }